import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1),
    paddingLeft: theme.spacing(2),
  },
}));

const SmallBreadcrumbs = withStyles(theme => ({
  root: {
    fontSize: theme.typography.pxToRem(13),
    fontWeight: theme.typography.fontWeightMedium
  }
}))(Breadcrumbs)

const SmallTypography = withStyles(theme => ({
  root: {
    fontSize: theme.typography.pxToRem(13),
  }
}))(Typography)

export default function ProductBreadcrumbs({ breads }) {
  const classes = useStyles();
  const lastItem = [...breads].pop();
  const breadList = breads.slice(0, breads.length-1);

  const handleClick = (e) => {
    e.preventDefault();
  }

  return (
    <div className={classes.root}>
      <SmallBreadcrumbs separator="" classes={classes.nav} aria-label="breadcrumb">
        {breadList.map((bread, index) => (
          <Link href="#" key={index} color="inherit" onClick={handleClick}>{bread}</Link>
        ))}
        <SmallTypography color="textPrimary">{lastItem}</SmallTypography>
      </SmallBreadcrumbs>
    </div>
  );
}
