const SearchListItem = ({text, desc }) => {
  const clz = useStyles();
  return (
    <li className={clz.li}>
      <a href="#" className={clz.liAnchor}>
        <div className={clz.anchorIcon}>
          <img className={clz.clockIcon} alt="" src="images/clock-icon.png"/>
        </div>
        <div className={clz.anchorText}>{text}
          {desc ? <div className={clz.anchorDec}>{desc}</div> : ''}
        </div>
      </a>
    </li>
  )
}

const ProductSearchBar = ({ placeholder }) => {
  const [expanded, setExpanded] = useState(false);

  const clz = useStyles();
  // const placeholder = 'Search for products, brands and more';

  const handleSubmit = (e) => {
    e.preventDefault();
  }

  return (
    <div className={clz.root}>
      <form className={clz.form} onSubmit={handleSubmit}>
        <div className={clz.searchBlk}>
          <div className={clz.inputWpr}>
            <input type="text" className={clz.input} name="q" autoComplete="off" 
              onFocus={e =>setExpanded(true)} 
              onBlur={e =>setExpanded(false)} 
              title={placeholder} placeholder={placeholder}/>
          </div>
          <button className={clz.button}>
            <SearchSvgIcon color="#2874F1"/>
          </button>
        </div>
        <ul className={classnames(clz.ul, {expanded})}>
          <SearchListItem text="128gb solid state drive for laptop"/>
          <SearchListItem text="128gb  ssd for laptop"/>
          <SearchListItem text="m2 ssd for laptop" desc="in Internal Hard Drive"/>
          <SearchListItem text="ssd for laptop"/>
          <SearchListItem text="500 gb ssd for laptop" desc="in Internal Hard Drive"/>
        </ul>
      </form>
    </div>
  );
}
 
