import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Slider, Input } from '@material-ui/core';
import { filters } from '../data/categories';
import { FilterBox, CheckboxControl } from './CoreFilters';


const useStyles = makeStyles(theme => ({
  wpr: {
    position: 'relative',
    background: '#fff',
    boxShadow: '0 1px 8px 0 rgba(0,0,0,.06)',
  },
  section: {
    padding: theme.spacing(2),
    borderBottom: '1px solid #f0f0f0',
  },
  filterLabel: {
    fontSize: 18,
    fontWeight: theme.typography.fontWeightBold,
  },
  flexRow: {
    display: 'flex',
  },
  input: {
    width: 42,
  },
}));

const CheckboxFilter = ({ label, items }) => {
  return (
    <FilterBox label={label}>
      <CheckboxControl name={label} items={items}/>
    </FilterBox>
  )
}

const PriceFilter = ({ label }) => {
  const clz = useStyles();

  const [value, setValue] = useState([20, 37]);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleSliderChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleInputChange = (event) => {
    setValue(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleBlur = () => {
    if (value < 0) {
      setValue(0);
    } else if (value > 100) {
      setValue(100);
    }
  };

  const valuetext = value => `${value}C`;

  return (
    <FilterBox label={label}>
      <Slider
        value={value}
        onChange={handleChange}
        valueLabelDisplay="auto"
        aria-labelledby="range-slider"
        getAriaValueText={valuetext}
      />
      <div className={clz.flexRow}>
        <Input
          className={clz.input}
          value={value}
          margin="dense"
          onChange={handleInputChange}
          onBlur={handleBlur}
          inputProps={{
            step: 10,
            min: 0,
            max: 100,
            type: 'number',
            'aria-labelledby': 'input-slider',
          }}
        />
      </div>
    </FilterBox>
  ) 
}


const ProductFilters = () => {
  const clz = useStyles();
  return (
    <div className={clz.wpr}>
      <section className={clz.section}>
        <span className={clz.filterLabel}>Filters</span>
      </section>
      <PriceFilter label="Price"/>
      {Object.keys(filters).map((key, index) => (
        <CheckboxFilter key={index} items={filters[key]} label={key}/>
      ))}
    </div>
  );
}
 
export default ProductFilters;