import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Card, CardActionArea, CardContent, CardMedia, Button, Typography, CardActions } from '@material-ui/core';
import { Link, useRouteMatch } from 'react-router-dom';
import { toDashName } from '../utils/utils';

const useStyles = makeStyles(theme => ({
  root: {
    maxWidth: `calc(20% - ${theme.spacing(1)}px)`,
    boxSizing: 'border-box',
    marginRight: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  anchor: {
    color: 'inherit',
    textDecoration: 'none',
  }
}));

export default function ProductCard({ img, title }) {
  const clz = useStyles();
  const { url } = useRouteMatch();
  const name = toDashName(title);

  return (
    <Card className={clz.root}>
      <Link className={clz.anchor} to={`${url}/${name}`}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt={title}
          height="200"
          image={img}
          title={title}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging
            across all continents except Antarctica
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Available
        </Button>
        
        <Button size="small" color="secondary">
          Not Available
        </Button>
      </CardActions>
      </Link>
    </Card>
  );
}