import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  root: {
    padding: `0 ${theme.spacing(1)}px 0 ${theme.spacing(2)}px`,
    display: 'flex',
  },
  h1: {
    fontSize: theme.typography.htmlFontSize,
    margin: `0 ${theme.spacing(1)}px 0 0`,
  },
  rangeDec: {
    color: '#878787',
    fontSize: theme.typography.pxToRem(12)
  }
}));

export default function ProductShowingRange({ label, from, to, total }) {
  const clz = useStyles();

  const showingProductsRage = () => {
    return `(Showing ${from}  ${to} products of ${total} products)`;
  }

  return (
    <div className={clz.root}>
      <h1 className={clz.h1}>{label}</h1>
      <span className={clz.rangeDec}>{showingProductsRage()}</span>
    </div>
  );
}
