import React from 'react';
import { MuiThemeProvider } from '@material-ui/core/styles'
import {
  BrowserRouter,
  Switch,
  Route,
  Link
} from 'react-router-dom';

import './App.css';
import HeaderNavBar from './header/HeaderNavBar';
import theme from './theme';
import { tabs, categories } from './data/categories';
import products from './data/products';
import LandingPage from './pages/LandingPage';
import ProductsPage from './pages/ProductsPage';
import ProductsDetailPage from './pages/ProductDetailPage';

function App() {
  const productsConfig = {tabs, categories, products};

  return (
    <>
      <MuiThemeProvider theme={theme}>
        <BrowserRouter>
          <Switch>
            <Route exact path="/">
              <HeaderNavBar/>
              <LandingPage {...productsConfig}/>
            </Route>
            <Route exact path="/:productId">
              <HeaderNavBar/>
              <ProductsPage {...productsConfig}/>
            </Route>
            <Route path="/:productId/:details">
              <ProductsDetailPage />
            </Route>
          </Switch>
        </BrowserRouter>
      </MuiThemeProvider>
    </>
  );
}

export default App;
