import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Checkbox, FormGroup, FormControlLabel, Slider } from '@material-ui/core';
import { filters } from '../data/categories';
import classnames from 'classnames'

const useStyles = makeStyles(theme => ({
  section: {
    padding: theme.spacing(2),
    borderBottom: '1px solid #f0f0f0',
  },
  spacer: {
    width: theme.spacing(1),
    background: theme.color.body,
  },
  product: {
    flex: 1,
    boxShadow: theme.product.boxShadow
  },
  flexCenter: {
    display: 'flex',
    justifyContent: 'space-between',
    paddingBottom: theme.spacing(1),
  },
  secLabel: {
    fontSize: 13,
    fontWeight: theme.typography.fontWeightBold,
    textTransform: 'uppercase',
    letterSpacing: '.3px',
  },
  sectionPanel: {
    height: 'auto',
    opacity: 1,
    transition: `height .3s ${theme.transitions.easing.easeOut}`,

    '&.expanded': {
      height: 0,
      opacity: 0,
    }
  },
  expander: {
    display: 'flex',
    justifyContent: 'center',
    width: 20
  },
  expanded: {
    display: 'inline-block',
    cursor: 'pointer',
    width: 6,
    height: 11,
  },
}));

const ExpanderIcon = ({ onClick = () => {}, isExpanded }) => {
  const clz = useStyles();
  return (
    <div className={clz.expander} onClick={onClick}>
      <svg width="16" height="27" viewBox="0 0 16 27"
        xmlns="http://www.w3.org/2000/svg" className={clz.expanded}
        style={{transform: isExpanded ? 'rotate(90deg)' : 'rotate(-90deg)'}}>
        <path d="M16 23.207L6.11 13.161 16 3.093 12.955 0 0 13.161l12.955 13.161z" fill="#878787"></path>
      </svg>
    </div>
  )
}

export const CheckboxControl = ({items, ...props}) => {
  return (
    <FormGroup>
      {items.map((item, index) => (
        <FormControlLabel key={index} label={item}
          control={<Checkbox {...props} color="primary"/>}
        />
      ))}
    </FormGroup>
  )
}

export const FilterBox = ({ label, children }) => {
  const clz = useStyles();
  const [expanded, setExpanded] = useState(false);

  const handleChange = () => {
    setExpanded(prev => !prev);
  }

  return (
    <section className={clz.section}>
      <div className={clz.flexCenter}>
        <span className={clz.secLabel}>{label}</span>
        <ExpanderIcon onClick={handleChange} isExpanded={expanded}/>
      </div>
      <div className={classnames(clz.sectionPanel, {expanded})}>
        <div className={clz.secrtionInnr}>
          {children}
        </div>
      </div>
    </section>
  )
}

