import React, { useState } from 'react';
import { Paper, Tabs, Tab } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

const ProductTabs = withStyles(theme => ({
  root: {
    minHeight: 40,
  },
  indicator: {
    backgroundColor: theme.color.active,
  },
}))(Tabs);

const ProductTab = withStyles(theme => ({
  root: {
    textTransform: 'none',
    minHeight: 40,
    padding: '0 12px',
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(1),

    '&:hover': {
      opacity: 1,
    },
    '&$selected': {
      color: theme.color.active,
      fontWeight: theme.typography.fontWeightMedium,
    },
    '&:focus': {
      color: theme.color.active,
    },
  },
  selected: {},
}))(Tab);

export default function SortByTabs({items}) {
  const [value, setValue] = useState(2);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper square>
      <ProductTabs
        value={value}
        indicatorColor="primary"
        textColor="primary"
        onChange={handleChange}
        aria-label="sort by filters"
      >
        <ProductTab label="Sort By" disabled />
        {items.map((item, index) => (
          <ProductTab key={index} label={item}/>
        ))}
      </ProductTabs>
    </Paper>
  );
}